<script>
import Velocity from "velocity-animate";
import store from "../store";
import router from "../router";

const default_layout = "default";
export default {
  components: {},

  data() {
    return {
      isLoad: false,
      isEvent: false,
      activeClass: "cancel-transition",
      errorClass: "",
      hasError: false,
      isTitle: "title",
      istitleSub: "sub",
      appName: "HAEMILSOFT",
      sidebar: false,
      transitionName: "slide-top",
      locale: "en",
      colorItem: [
        "#4bb4e7",
        "#1e9cd9",
        "#0277bd",
        "#2f5aa8",
        "#242424",
        "#313131"
      ],
      blocks: [
        {
          speed: [640, 375, 375, 375, 375, 375, 375, 375],
          width: [1500, 375, 375, 375, 375, 375, 375, 375],
          height: [640, 320, 320, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277bd",
            "#242424",
            "#242424",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 640, 640, 320, 375, 337, 375, 375],
          width: [375, 375, 375, 188, 375, 375, 375, 375],
          height: [640, 640, 640, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277BD",
            "#242424",
            "#242424",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 375, 375, 320, 320, 320, 320, 375],
          width: [1125, 375, 375, 375, 1125, 375, 375, 375],
          height: [320, 320, 320, 320, 320, 320, 320, 160],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277BD",
            "#242424",
            "#313131",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 337, 640, 337, 337, 337, 337, 320],
          width: [750, 375, 375, 375, 375, 375, 375, 188],
          height: [640, 320, 640, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277BD",
            "#242424",
            "#313131",
            "#313131",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        }
      ],
      items: []
    };
  },
  created() {
    // 뷰가 생성되고 데이터가 이미 감시 되고 있을 때 데이터를 가져온다.
    // this.fetchData()
  },
  watch: {
    // 라우트가 변경되면 메소드를 다시 호출됩니다.
    // '$route': 'fetchData'
    /* locale (val) {
      this.$i18n.locale = val
    } */
  },
  mounted() {
    const visualArray = this.fn_array(this.$refs["visual"]);
    let nameT = null;
    let nameF = null;
    let idx = null;
    // console.log(visualArray)

    router.beforeEach((to, from, next) => {
      idx = to.meta.index;
      this.isTitle = to.meta.title;
      this.isTitleSub = to.meta.titleSub;
      this.isLoad = true;
      // console.log(to.path)
      /*
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      */
      if (to.path.split("/").length > 2) {
        this.transitionName = to.meta.transition;
        // console.log(to.meta.transition)
        next();
      } else {
        const tr = this.transitionName.split("-");
        this.transitionName = tr[0];
        if (tr[1] == "top") {
          this.transitionName = this.transitionName + "-" + "bottom";
        } else if (tr[1] == "right") {
          this.transitionName = this.transitionName + "-" + "left";
        } else if (tr[1] == "bottom") {
          this.transitionName = this.transitionName + "-" + "top";
        } else if (tr[1] == "left") {
          this.transitionName = this.transitionName + "-" + "right";
        } else if (tr[1] == "in") {
          this.transitionName = this.transitionName + "-" + "out";
        } else if (tr[1] == "out") {
          this.transitionName = this.transitionName + "-" + "in";
        } else {
          this.transitionName = "fade";
        }
        this.fn_sub(next, this.blocks[idx], visualArray, idx);
      }
    });

    router.afterEach((to, from) => {
      // console.log('afterEach')
      this.isLoad = false;
    });
  },
  computed: {
    /* key() {console.log('hh')
      return this.$route.meta.key !== undefined
      	? this.$route.meta.key
        : this.$route
    } */
  },
  methods: {
    fetchData() {
      // 데이터 가져오기 위한 유틸리티/API 래퍼로 변경합니다.
    },
    fn_array(elements) {
      let arr = [];
      for (var i = 0; i < elements.childNodes.length - 1; i++) {
        arr[i] = elements.childNodes[i];
      }
      return arr;
    },
    fn_sub: function(next, blockSize, visual, idx) {
      // this.fn_size(visual, blockSize, 0, next);
      // const loop = this.fn_test();
      const vLangth = visual.length - 1;
      if (idx > 0) {
        visual.forEach(function(element, index) {
          // console.log(element,' / ',index,' / ',element.children)
          if (element.children.length > 0) {
            //element.children[0].classList.add("hide");
          }
          // element.style.willChange = 'width, height, transform, opacity';
          Velocity(
            element,
            {
              width: blockSize.width[index],
              height: blockSize.height[index],
              "max-width": blockSize.width[index],
              "max-height": blockSize.height[index]
            },
            {
              duration: 300,
              // easing: [0.42, 0.0, 0.58, 1.0],
              easing: [0.25, 0.8, 0.5, 1],
              complete: function() {
                // element.style.willChange = 'auto';
                if (element.children.length > 0) {
                  element.children[0].classList.remove("hide");
                }
                //element.removeAttribute("style");
                if (index == vLangth || idx == 0) {
                  next();
                }
              }
            }
          );
        });
      } else {
        //visual[0].children[0].classList.add('hide')
        Velocity(
          visual[0],
          {
            width: blockSize.width[0],
            height: blockSize.height[0],
            "max-width": blockSize.width[0],
            "max-height": blockSize.height[0]
          },
          {
            duration: 500,
            // easing: [0.25, 0.8, 0.5, 1],
            easing: [0.25, 0.8, 0.5, 1],
            complete: function() {
              //visual[0].children[0].classList.remove('hide')
              //visual[0].removeAttribute("style");
              next();
            }
          }
        );
      }
    }
  }
};
</script>

<style lang="scss">
@import "../assets/styles/transition.scss";
$block-width: 375px;
$block-height: 320px;

$block-width-half: 187px;
$block-height-half: 160px;

$block-width-2x: 750px;
$block-height-2x: 640px;

$block-width-3x: 1125px;
$block-width-4x: 1500px;

.visual {
  position: relative;
  width: $block-width-4x;
  height: $block-height-2x;
  min-width: $block-width-4x;
  padding: 0;
  overflow: hidden;
  /*outline: 10px solid #efefef;*/
  .block {
    max-width: $block-width;
    max-height: $block-height;
    width: $block-width;
    height: $block-height;
    background-color: #fff;
    border: 1px solid #fff;
    position: absolute;
    border-bottom-width: 0;
    border-right-width: 0;
    /*overflow: hidden;*/
    /*transition: 300ms cubic-bezier(0.59, 0.12, 0.34, 0.95);
        transition-property: opacity, transform, width, height;*/
    z-index: 0;

    display: flex;
    flex-direction: column;
    /*align-items: center;
        justify-content: center;*/
  }
  .block:nth-child(-n + 4) {
    border-top-width: 0px;
  }
  .block:first-child,
  .block:nth-child(5n) {
    border-left: 0px;
  }

  /*.isLoad::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 0;
        background-color: #F00 !important;
        transform-origin: left top;
        transition-property: left, width, transform;
        transition-duration: .2s;
        transition-timing-function: cubic-bezier(0.25, 0.8, 0.5, 1);
        will-change: auto;
    }*/
  .isLoad:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    z-index: 1;
    width: 0%;
    background-color: rgba(0, 0, 0, 1);
    transform-origin: left top;
    transition-property: left, width, transform;
    transition-duration: 0.2s, 0.2s;
    transition-timing-function: cubic-bezier(1, 0.5, 0.8, 0.25),
      cubic-bezier(0.25, 0.8, 0.5, 1);
    will-change: auto;
  }
  .block0 {
    background: #1e9cd9;
  }
  .block0.isLoad:after {
    background: #1e9cd9;
  }
  .block1 {
    background: #0277bd;
  }
  .block1.isLoad:after {
    background: #0277bd;
  }
  .block2.isLoad:after {
    background: #242424;
  }
  .block2,
  .block3,
  .block6,
  .block7 {
    background: #242424;
  }
  .block4 {
    background: #efefef;
  }
  .block5 {
    background: #0277bd;
  }

  .block0 {
    z-index: 2;
  }
  .block1,
  .block4,
  .block2 {
    z-index: 1;
  }
  .block0,
  .block1,
  .block2,
  .block3 {
    top: 0px;
  }
  .block4,
  .block5,
  .block6,
  .block7 {
    top: $block-height;
  }

  .block0,
  .block4 {
    left: 0px;
  }
  .block1,
  .block5 {
    left: $block-width;
  }
  .block2,
  .block6 {
    left: $block-width-2x;
  }
  .block3,
  .block7 {
    left: $block-width-3x;
  }
}
.Landing {
  .block0 {
    max-width: $block-width-4x;
    max-height: $block-height-2x;
    width: $block-width-4x;
    height: $block-height-2x;
  }
  .block1,
  .block2,
  .block3,
  .block4,
  .block5,
  .block6,
  .block7 {
    max-width: $block-width;
    max-height: $block-height;
    width: $block-width;
    height: $block-height;
  }
}
.Service {
  .block0,
  .block1,
  .block2 {
    max-width: $block-width;
    max-height: $block-height-2x;
    width: $block-width;
    height: $block-height-2x;
  }
  .block3 {
    max-width: $block-width-half;
    max-height: $block-height;
    width: $block-width-half;
    height: $block-height;
  }
  .block4,
  .block5,
  .block6,
  .block7 {
    max-width: $block-width;
    max-height: $block-height;
    width: $block-width;
    height: $block-height;
  }
}
.About {
  .block0,
  .block4 {
    max-width: $block-width-3x;
    max-height: $block-height;
    width: $block-width-3x;
    height: $block-height;
  }
  .block7 {
    max-width: $block-width;
    max-height: $block-height;
    width: $block-width;
    height: $block-height-half;
  }
  .block1,
  .block2,
  .block3,
  .block5,
  .block6 {
    max-width: $block-width;
    max-height: $block-height;
    width: $block-width;
    height: $block-height;
  }
}
.Support {
  .block0 {
    max-width: $block-width-2x;
    max-height: $block-height-2x;
    width: $block-width-2x;
    height: $block-height-2x;
  }
  .block1,
  .block3,
  .block4,
  .block5,
  .block6 {
    max-width: $block-width;
    max-height: $block-height;
    width: $block-width;
    height: $block-height;
  }
  .block2 {
    max-width: $block-width;
    max-height: $block-height-2x;
    width: $block-width;
    height: $block-height-2x;
  }
  .block7 {
    max-width: $block-width-half;
    max-height: $block-height;
    width: $block-width-half;
    height: $block-height;
  }
}
.visual.active .isLoad:after,
.active.isLoad:after {
  width: 100%;
  will-change: width, height, opacity, transform;
}

.sub-title {
  position: absolute;
  width: auto;
  height: 64px;
  left: 50%;
  top: 50%;
  z-index: 10;
  transform-origin: 0%;
  transform: rotate(-90deg) translateX(-288px) translateY(-782px);
  /*transform: rotate(-90deg) translateX(-268px) translateY(-410px);*/
}

.display-1,
.headline,
.caption {
  padding: 0px;
}

.title-top {
  position: relative;
  display: inline-block;
  color: #1e9cd9;
}
.title-top:before {
  /*transition: all 320ms cubic-bezier(0.25, 0.8, 0.5, 1);*/
  background-color: #ffffff;

  transition-property: left, width;
  transition-duration: 0.2s, 0.4s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1),
    cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.title-des {
  position: relative;
  display: block;
  color: #242424;
  height: 20px;
  text-decoration: none !important;
  margin-top: 2px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #0277bd;
  color: #fff;
}
.title-des:before {
  /*transition: all 400ms cubic-bezier(0.25, 0.8, 0.5, 1);*/
  background-color: #0277bd;
  transition-property: left, width;
  transition-duration: 0.3s, 0.3s;
  transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94),
    cubic-bezier(0.215, 0.61, 0.355, 1);
}

.title-top:before,
.title-des:before {
  content: attr(data-title);
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 100%;
  z-index: 1;

  width: 0%;

  overflow: hidden;
  color: transparent;
}
.sub-title.active .title-top:before,
.sub-title.active .title-des:before {
  width: 100%;
}

.Landing .hide {
  display: block;
}
.hide {
  display: none;
}
</style>
