<template>
  <v-app>
    <guide axisV></guide>
    <header>
      <v-navigation-drawer
        dark
        app
        disable-resize-watcher
        fixed
        :clipped="$vuetify.breakpoint.lgAndUp"
        v-model="sidebar"
        right
        flat
        overflow
      >
        <menu-bar menuPosition="side" gray></menu-bar>
      </v-navigation-drawer>
      <v-toolbar app flat :clipped-left="$vuetify.breakpoint.lgAndUp" height="64px">
        <v-toolbar-title>{{appName}}</v-toolbar-title>
        <v-spacer></v-spacer>
        
        <menu-bar
          v-bind:class="{ active: position }"
          class="hidden-sm-and-down"
          menuPosition="top"
          v-if="this.$route.meta.layout === 'default'"
        ></menu-bar>
        
        <v-btn class="v-btn--icon" fab round flat @click="fn_back" v-else>
          <v-icon class="clear">clear</v-icon>
        </v-btn>

        <v-toolbar-side-icon class="sidebar hidden-md-and-up" @click.stop="sidebar = !sidebar" v-if="this.$route.meta.layout === 'default'"></v-toolbar-side-icon>

      </v-toolbar>
    </header>

    <v-content>
      <transition v-bind:name="transitionName" appear>
        <component :is="layout">
          <router-view></router-view>
        </component>
      </transition>
    </v-content>

    <footer-bar></footer-bar>
    <!-- <v-footer app></v-footer> -->
  </v-app>
</template>

<script>
import store from "./store";
import router from "./router";
import Guide from "@/components/Guide.vue";
import MenuBar from "@/components/Menus.vue";
import FooterBar from "@/components/Footer.vue";

const default_layout = "default";

export default {
  components: {
    Guide,
    MenuBar,
    FooterBar
  },
  computed: {
    layout() {
      return (this.$route.meta.layout || default_layout) + "-layout";
    }
  },
  data() {
    return {
      isMobile: false,
      sidebar: false,
      position: false,
      appName: "RYZEN SOFT",
      activeClass: "cancel-transition",
      errorClass: "",
      transitionName: "zoom-in"
    };
  },
  beforeDestroy() {
    if (typeof window !== "undefined") {
      window.removeEventListener("resize", this.fn_resize, { passive: true });
    }
  },
  created() {
    // console.log('created')
  },
  mounted() {
    this.fn_resize();
    window.addEventListener("resize", this.fn_resize, { passive: true });

    router.beforeEach((to, from, next) => {
      if (to.meta.index > from.meta.index) {
        this.position = true;
      } else {
        this.position = false;
      }
      if (to.meta.transition) {
        this.transitionName = "zoom-in";
      } else {
        this.transitionName = "zoom-out";
      }
      next();
    });
  },
  methods: {
    fn_resize() {
      this.isMobile = window.innerWidth < 750;
      // console.log(window.innerWidth,' / ',this.isMobile)
    },
    fn_back: function() {
      router.go(-1);
    }
  }
};
</script>

<style lang="scss">
@import "./assets/styles/transition.scss";
html {
  body {
    .application {
      width: 100%;
      .application--wrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100%;
        header {
          width: 100%;
          max-width: 1500px;
          aside {
            /*border: 2px solid #F00;*/
          }
          nav.v-toolbar {
            width: 100%;
            position: relative;
            .v-toolbar__content{
              .sidebar{
                
              }
              .v-btn__content{
                  .v-icon{
                      color: #1b8ffe !important;
                  }
                }
            }
          }
        }
        main {
          padding: 0 !important;
          flex: 0 0 auto;
          max-width: 1500px;
          min-height: 640px;
          .v-content__wrap {
            display: flex;
            justify-content: center;
            align-items: start;
          }
        }
        footer {
          width: 100%;
          max-width: 1500px;
          position: relative !important;
        }
      }
    }
  }
}
.theme--light.application {
  background: transparent !important;
  color: rgba(255, 255, 255, 0.87) !important;
}
.theme--light.v-icon {
  color: rgba(255, 255, 255, 0.87) !important;
}
.theme--light.v-input:not(.v-input--is-disabled) input,
.theme--light.v-input:not(.v-input--is-disabled) textarea {
  color: rgba(255, 255, 255, 0.87) !important;
}
.theme--light.v-label {
  color: rgba(255, 255, 255, 0.87) !important;
}
.theme--light.v-select .v-select__selections {
  color: rgba(255, 255, 255, 0.87) !important;
}
.theme--light.v-toolbar,
.theme--light.v-footer {
  background-color: transparent !important;
}

.theme--light.v-icon.clear {
  color: rgba(0, 0, 0, 0.87) !important;
}

.v-btn-toggle .v-btn:not(:last-child) {
  border-right-width: 0 !important;
}
.v-toolbar__content .v-btn {
  font-weight: 600;
}
.v-toolbar__content .v-btn:before {
  opacity: 1 !important;
}

.v-btn {
  font-family: "Noto Sans KR", sans-serif !important;
  font-size: 16px;
}
.v-item-group.active .v-btn:before {
  right: 0;
  left: unset;
}
.v-btn:before {
  content: "";
  border-bottom: 2px solid #4bb4e7;
  position: absolute;
  left: 0;
  top: 0;
  width: 0% !important;
  opacity: 0.69 !important;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.v-btn--active:before,
.v-btn:focus:before {
  width: 100% !important;
}
.v-btn--round {
  width: 36px !important;
  height: 36px !important;
}
.v-btn--round:before {
  border: none;
}


